<template>
	<div class="home">
		<nav>
			<div class="nav-bar">
				<div class="user" @click="isShowHistory = !isShowHistory">
					<van-icon name="user-circle-o" size="28" />
				</div>
				<div class="tabs">
					<div class=" tab">
						<span>
							<router-link to="/found">发现</router-link>
						</span>
					</div>

					<div class=" tab">
						<span>
							<router-link to="/recommend">推荐</router-link>
						</span>
					</div>
					<div class=" tab">
						<span>
							<router-link to="/handpick">每日精选</router-link>
						</span>
					</div>
				</div>
				<div class="search" @click="$router.push({ path: 'search' })">
					<van-icon name="search" size="24" />
				</div>
			</div>
		</nav>
		<keep-alive>
			<router-view></router-view>
		</keep-alive>
		<div class="history" :class="{ active: isShowHistory }" >
			<div class="pannel">
				<div class="title">
					<h3>观看记录</h3>
					<div class="clear" @click="watchHistory = []">
						<span>清除</span>
						<van-icon name="clock-o" size="18" />
					</div>
				</div>

				<ul v-if="watchHistory">
					<li
						v-for="(item, index) in watchHistory"
						:key="index"
						@click="getRoute('player', 'setVideoInfo', item)"
					>
						<!-- {{ item.type }} -->
						<VideoSmallCard :item="item"></VideoSmallCard>
					</li>
				</ul>
			</div>
			<div class="mask" @click="isShowHistory = false"></div>
		</div>
	</div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import VideoSmallCard from "@/components/VideoSmallCard.vue";
export default {
	name: "home",
	components: {
		VideoSmallCard
	},

	data() {
		return {
			isShowHistory: false
		};
	},
	computed: {
		watchHistory: {
			get() {
				if (this.$store.state.watchRecord.length) {
					return this.$store.state.watchRecord;
				} else {
					return JSON.parse(
						window.localStorage.getItem("watchRecord")
					);
				}
			},
			set(newValue) {
				window.localStorage.setItem(
					"watchRecord",
					JSON.stringify(newValue)
				);
				this.$store.commit({
					type: "syncWatchRecord",
					aoumt: newValue
				});
			}
		}
	},
	methods: {
		getRoute(path, method, id) {
			// window.console.log(id);
			this.$store.commit({
				type: method,
				aoumt: id
			});
			this.$router.push(path);
		}
	},
	created() {
		this.$nextTick(() => {
			let pannel = document.querySelector(".pannel ul");
			pannel.onscroll = function(e) {
				e.preventDefault();
				e.stopPropagation();
			};
		});
	}
};
</script>
