import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		classificationId: null,
		videoInfo: null,
		watchRecord: []
	},
	mutations: {
		setClassificationId(state, playload) {
			state.classificationId = playload.aoumt;
		},
		setVideoInfo(state, playload) {
			state.videoInfo = playload.aoumt;
		},
		syncWatchRecord(state, playload) {
			state.watchRecord=playload.aoumt;
		}
	},
	actions: {},
	modules: {}
});
