<template>
	<div class="video-small-card">
		<template v-if="item.type == 'videoSmallCard'">
			<div class="item">
				<div
					class="cover"
					:style="{
						'background-image': `url(${item.data.cover.detail})`
					}"
				>
					<!-- <img :src="item.data.cover.detail" alt=""> -->
					<span class="dt">{{
						translateTime(item.data.duration)
					}}</span>
				</div>
				<div class="info">
					<h3 class="title">{{ item.data.title }}</h3>
					<p>{{ "#" + item.data.category }}</p>
				</div>
			</div>
		</template>
		<template v-else-if="item.type == 'followCard'||item.type == 'autoPlayFollowCard'">
			<div class="item">
				<div
					class="cover"
					:style="{
						'background-image': `url(${item.data.content.data.cover.detail})`
					}"
				>
					<!-- <img :src="item.data.cover.detail" alt=""> -->
					<span class="dt">{{
						translateTime(item.data.content.data.duration)
					}}</span>
				</div>
				<div class="info">
					<h3 class="title">{{ item.data.content.data.title }}</h3>
					<p>{{ "#" + item.data.content.data.category }}</p>
				</div>
			</div>
		</template>
	</div>
</template>

<script>
export default {
	name: "VideoSmallCard",
	props: {
		item: Object
	},
	methods: {
		translateTime(time) {
			// window.console.log(time)
			let minute =
				parseInt(time / 60) > 10
					? parseInt(time / 60)
					: "0" + parseInt(time / 60);
			let second =
				parseInt(time % 60) > 10
					? parseInt(time % 60)
					: "0" + parseInt(time % 60);
			return minute + ":" + second;
		}
	}
};
</script>

<style lang="less" scoped>
.video-small-card {
	padding: 5px 0;
	box-sizing: border-box;
	.item {
		display: flex;
		width: 100%;
		.cover {
			height: 74px;
			width: 126px;
			position: relative;
			background-size: cover;
			border-radius: 3px;
			margin-right: 10px;
			span.dt {
				position: absolute;
				bottom: 5px;
				right: 5px;
				background: rgba(0, 0, 0, 0.527);
				color: white;
				font-size: 12px;
				padding: 2px;
			}
		}
		.info {
			flex: 1;
			text-align: left;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding-bottom: 5px;
			h3.title {
				font-size: 14px;
				font-weight: bold;
				margin-bottom: 10px;
			}
			p {
				font-size: 10px;
				color: rgb(121, 121, 121);
			}
		}
	}
}
</style>
